
#pine {
  font-size: 2em;
  font-weight: 500;
  color: var(--acorn-green-M1);
}
#oak {
  font-size: 2em;
  font-weight: 500;
  color: var(--acorn-brown-D1);
}
#logo {
  display: inline-block;
}
.sentenceBkg {
  background-color: var(--pine-green-L1);
  border: 1px var(--acorn-green-M1) solid;
  border-radius: 0.5em;
  margin: 0 15px;
  padding: 30px;
  overflow-x: scroll;
}
#sentences-container {
  min-width: 100px;
  width: 100%;
  height: 100%;
  overflow: scroll;
}
#sentences-list {
  text-align: left;
  font-size: 1.1em;
  padding-top: 15px;
}
#sentences-list li {
  margin-bottom: 15px;
}
.deprelText {
  font-size: 13.8px;
  font-family: Arial, Tahoma, sans-serif;
}
#editor-container {
  height: 100%;
  min-width: 50px;
  width: 100%;
  overflow: scroll;
}
#editorToolbar {
  position: fixed;
  top: 70px;
  right: 0;
  height: 23px;
  text-align: left;
  z-index: 5;
}
#editorToolbar > a {
  color: var(--acorn-brown-D2);
  margin: 0;
  padding: 1px 8px;
  font-size: 10pt;
  background-color: orange;
  border: 1px orange solid;
  border-radius: 0;
  opacity: 0.85;
}
#editor {
  height: 100%;
  min-width: 50px;
  overflow: scroll;
}
.gutter {
  background-color: var(--acorn-gray-M1);
  opacity: 0.8;
  cursor: col-resize;
}



